import Hi from './Hi.js';
let hi = new Hi();
class ImageLoader{
  constructor(){
    this._store = {};
  }

  load(images){
    let total = 0;
    let loaded = 0;
    Object.getOwnPopertyNames(images).forEach((key) => {
      total++;
      let url = images[key];

      let img = new Image();
      img.url = url;
      image.onload = () => {
        loaded++;
        this._store[key] = img;
        if(total === loaded)
          cb();
      }

      image.err = (e)=>{
        cb();
      }
    });
  }
}

const loader = new ImageLoader();
loader.on('progress', (current, total)=>{
  console.log(`$(current) of $(total) complete`);
});

loader.load({
  'vase': '',
  'image':'',
});


